<template>
  <div 
    class="body"
  >
    <div 
      class="background" 
      @click="highLightAll"
    />
    <div class="content">
      <div 
        class="left"
        :style="{width: listWidth}"
      >
        <div
          v-for="list in chapters"
          :key="list"
          class="content-item"
        >
          <p lang="en">
            <span
              v-for="item in list.left.title"
              :key="item"
              class="letter-item"
              :class="[item.size.value, item.type, isActiveColor(item) ? item.color.value : '' ]"
              @click="activeColor(item)"
            >{{ item.content + ' ' }}</span>
          </p>
          <p lang="en">
            <span
              v-for="item in list.left.content"
              :key="item"
              class="letter-item"
              :class="[item.size.value, item.type, isActiveColor(item) ? item.color.value : '' ]"
              @click="activeColor(item)"
            >{{ item.content + ' ' }}</span>
          </p>
        </div>
      </div>

      <div 
        class="right"
        :style="{width: listWidth}"
      >
        <div
          v-for="list in chapters"
          :key="list"
          class="content-item"
        >
          <p lang="en">
            <span
              v-for="item in list.right.title"
              :key="item"
              class="letter-item"
              :class="[item.size.value, item.type, isActiveColor(item) ? item.color.value : '' ]"
              @click="activeColor(item)"
            >{{ item.content + ' ' }}</span>
          </p>
          <p lang="en">
            <span
              v-for="item in list.right.content"
              :key="item"
              class="letter-item"
              :class="[item.size.value, item.type, isActiveColor(item) ? item.color.value : '' ]"
              @click="activeColor(item)"
            >{{ item.content + ' ' }}</span>
          </p>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, computed } from 'vue';
import { textList } from '../../static/text'
import { textCList } from '../../static/textC'
import { titleList } from '../../static/title'
import { titleCList } from '../../static/titleC'

// 文本预处理
const chapters = []

const colorList = [[], [], []]
const colorCList = [[], [], []]
const sizeList = [[], [], []]
const sizeCList = [[], [], []]

for(let i = 0; i < textList.length; i++) {
  const textLists = [textList[i], textCList[i], titleList[i], titleCList[i]]
  textLists.forEach(list => {
    list.forEach(item => {
      let color = ref('red')
      let colorIndex = 0
      const size = item.size
      switch(size) {
        case 'b':
          colorIndex = 0
          color.value = 'red'
          break
        case 'cb':
          colorIndex = 0
          color.value = 'blue'
          break
        case 'm':
        case 'cm':
          colorIndex = 1
          color.value = 'yellow'
          break
        case 's':
          colorIndex = 2
          color.value = 'blue'
          break
        case 'cs':
          colorIndex = 2
          color.value = 'red'
          break
      }
      if(size[0] == 'c') {
        colorCList[colorIndex].push(item)
        sizeCList[colorIndex].push(item)
      }else {
        colorList[colorIndex].push(item)
        sizeList[colorIndex].push(item)
      }
      item.color = color
      item.size = ref(item.size)
    })
  })

  chapters.push({
    left: {
      title: textLists[2],
      content: textLists[0]
    },
    right: {
      title: textLists[3],
      content: textLists[1]
    },
  })
}

// 激活状态
const active = ref('red')
const activeC = ref('blue')
const isHighLight = ref(false)

// 高亮颜色类
const isActiveColor = computed(() => (item) => {
  if(!isHighLight.value) return true
  let flag = active
  if(item.size.value[0] == 'c')
    flag = activeC

  if(item.color.value == flag.value) return true
  return false
})

// 尺寸列表刷新
const refreshSizeList = function(lists, isC) {
  lists[0].forEach(item => {
    item.size.value = isC ? 'cb' : 'b'
  })
  lists[1].forEach(item => {
    item.size.value = isC ? 'cm' : 'm'
  })
  lists[2].forEach(item => {
    item.size.value = isC ? 'cs' : 's'
  })
}

// 中变大
const m2b = function(lists) {
  let list = lists[0]
  lists[0] = lists[1]
  lists[1] = lists[2]
  lists[2] = list
}

// 小变大
const s2b = function(lists) {
  let list = lists[0]
  lists[0] = lists[2]
  lists[2] = lists[1]
  lists[1] = list
}

// 修改高亮颜色
const activeColor = function(item) {
  let flag = active
  let flagSize = sizeList
  let isC = false
  if(item.size.value[0] == 'c') {
    flag = activeC
    flagSize = sizeCList
    isC = true
  }
  
  
  // 高亮颜色
  flag.value = item.color.value

  // 修改大小
  const size = item.size.value
  if(size == 'b' || size == 'cb') return
  switch(size) {
    case 'm':
    case 'cm':
      m2b(flagSize)
      refreshSizeList(flagSize, isC)
      break
    case 's':
    case 'cs':
      s2b(flagSize)
      refreshSizeList(flagSize, isC)
      break
  }
}

// 高亮开关
const highLightAll = function() {
  isHighLight.value = !isHighLight.value
}

// 计算宽度
const listWidth = (window.innerWidth - 95) / 2 + 'px'
const getListWidth = function() {
  const width = window.innerWidth
  return (width - 95) / 2 + 'px'
}
</script>

